import { _decorator, Component, Node } from 'cc';
import { Command, DisableTouchMask, IsInChessFightScene, WaitEnterScene } from '../GuideCommand';
import { EventMgr } from '../../mgr/EventMgr';
import { LocalEvent } from '../../define/LocalEvent';
const { ccclass, property } = _decorator;

export const task_100 = {
    name: '第1场战斗',
    debug: false,
    condition: IsInChessFightScene,
    steps: [
        {
            // desc: 'step1',
            onStart(callback) {
                WaitEnterScene("FightScene", () => {
                    callback()
                })
            },
        },
        {
            onStart(callback) {
                callback()
            },
            //desc: step1 '欢迎来到疯狂棋士，您可以在棋盘中点亮的范围内放置您的棋子',
            delayTime: 1.5,
            command: {
                cmd: Command.Parallel, args: [
                    { cmd: Command.DialogueNonmodal, args: 100 },
                ]
            },
            onEnd(cb) {
                EventMgr.emitLocal(LocalEvent.HideDialogue)
                cb();
            }
        },
        {
            onStart(callback) {
                callback()
            },
            delayTime: 0.1,
            //desc: step13 '继续提升投弹棋子的等级',
            command: {
                cmd: Command.Parallel, args: [
                    { cmd: Command.ChessMove, args: { step: 5, id: 111 } },
                ]
            },
            onEnd(cb) {
                cb();
            },
        },
    ]
}


